.video {
    overflow: hidden;

    &__wrapper {
        padding: 50px 0;
        display: grid;
        grid-template-columns: 1fr 1.2fr;
        align-items: center;
        gap: 30px;

        @media screen and (max-width: 850px) {
            grid-template-columns: 1fr;
        }
    }

    

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;

        @include vp-1023 {
            font-size: 45px;
        }

        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0;

        @media screen and (max-width: 850px) {
            br {
                display: none;
            }
        }
    }

    .video-preview {
        // width: 660px;
        // height: 380px;
        max-width: 100%;
        height: auto;
        aspect-ratio: 660/380;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .video-play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        svg {
            transition: .3s;
        }
    }

    .video-play-btn:hover {
        svg {
            opacity: 0.8;
        }
    }
}