.tape {
    overflow: hidden;
    position: relative;

    &__wrapper {
        padding: 40px 0 70px;
    }
    &__slider {
        width: 100vw;
        position: relative;
        left: 50%;
        right: 50%;
        margin-left: -55vw;
        margin-right: -55vw;
        overflow: visible;
    }

    .tape__slider-content {
        display: flex;
        align-items: center;
        gap: 50px;
        will-change: transform;
        position: relative;
        z-index: 1;
        backface-visibility: hidden; 
        transform-style: preserve-3d;
    }

    .swiper-slide {
        width: auto;
    }

    @keyframes marquee {
        0% {
            transform: translateX(0);
        }
        100% {
            transform: translateX(-100%); 
        }
    }
}