.header {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 3;
  background-color: rgba(26, 26, 24, 1);
  &__wrapper {
    padding: 30px 0 15px;
  }



  &__center {
    display: flex;
    
    align-items: center;
    gap: 20px;

    @include vp-389 {
      gap: 10px;
    }
  }

  
  .header__logo {
    width: 160px;
    height: 50px;
    flex-shrink: 0;

    @include vp-389 {
      width: 130px;
      height: 40px;
    }
  }


  &__icons {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-left: 140px;

    @include vp-1279 {
      margin-left: 140px;
    }

    @media screen and (max-width: 1180px) {
      margin-left: 50px;
    }

    @include vp-1023 {
      margin: 0 auto;
    }

    @media screen and (max-width: 580px) {
      margin-top: 100px;
    }
  }

  &__icon {
    display: contents;
    svg {
      transition: .3s;
      width: 20px;
      height: 20px;
      flex-shrink: 0;
    }
  }

  &__icon:hover {
    svg {
      opacity: 0.8;
    }
  }

  &__phone,
  &__phone-mobile {
    font-family: 'Geologica';
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 150%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: rgba(255, 255, 255, 1);
    transition: .3s;
    white-space: nowrap;
  }

  &__phone {
    @media screen and (max-width: 590px) {
      font-size: 12px;
    }
  }

  &__phones-mobile {
    display: none;

    @media screen and (max-width: 580px) {
      display: block;
      margin: 0 auto;
    }
  }

  &__phone-mobile {
    @media screen and (max-width: 450px) {
      font-size: 12px;

      span {
        display: none;
      }
    }
  } 

  &__phone:hover {
    opacity: 0.8;
  }

  &__bottom {
    width: 100%;
    padding: 10px 0;
    transition: .3s;
    @include vp-1023 {
      transform: translateX(100%);
      position: fixed;
      z-index: 10;
      overflow-y: auto;
      overflow-x: hidden;
      top: 90px;
      left: 0;
      right: 0;
      bottom: 0;
      background: black;
      padding: 30px 20px;
    }
  }

  &.is-open .header__bottom {
    transform: translateX(0);
  }

  .main-nav__list {
    display: flex;
    align-items: center;
    gap: 30px;
    justify-content: end;

    @media screen and (max-width: 1180px) {
      gap: 20px;
    }

    @include vp-1023 {
      align-items: flex-start;
      flex-direction: column;
      margin-bottom: 20px;
    }
    
  }

  .main-nav__link {
    font-family: 'Geologica';
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 150%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: rgba(255, 255, 255, 1);
    transition: .3s;
    display: block;
    &:hover {
      opacity: 0.6;
    }

    @include vp-1023 {
      font-size: 18px;
    }
  }


  .main-nav__toggle {
    position: relative;
    display: none;
    width: 50px;
    height: 40px;
    padding: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    // -webkit-transition: opacity rgb(255, 255, 255);
    // -o-transition: opacity rgb(255, 255, 255);
    // transition: opacity rgb(255, 255, 255);
    @include vp-1023 {
      display: flex;
      margin-left: auto;
    }
  }
  
  .main-nav__toggle span {
    display: block;
    position: absolute;
    left: 10px;
    height: 5px;
    width: 35px;
    border-radius: 100px;
    background-color: rgba(255, 255, 255, 1);
    -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  .main-nav__toggle span:nth-child(1) {
    top: 8px;
  }
  
  .main-nav__toggle span:nth-child(2) {
    top: 50%;
  
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .main-nav__toggle span:nth-child(3) {
    bottom: 8px;
  }
  
  .main-nav__toggle.focus-visible:focus {
    opacity: 0.8;
  }

  &.is-open .main-nav__toggle span:nth-child(1) {
    top: 12px;
    // -webkit-transform: rotate(-47deg) translate(-5px, 11px);
    // -ms-transform: rotate(-45deg) translate(0, -50%);
    transform: rotate(-45deg) translate(0px, 5px);
    background: rgba(255, 255, 255, 1);
    height: 5px;
    width: 35px;
  }
  
  &.is-open .main-nav__toggle span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  
  &.is-open .main-nav__toggle span:nth-child(3) {
    top: 20px;
    -webkit-transform: rotate(45deg) translate(0, 50%);
    -ms-transform: rotate(45deg) translate(0, 50%);
    transform: rotate(45deg) translate(0px, -7px);
    background: rgba(255, 255, 255, 1);
    height: 5px;
    width: 35px;
  }

}