.pur {
    &__wrapper {
        padding: 100px 0 40px;
    }

    &__top {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin-bottom: 40px;

        
        @media screen and (max-width: 850px) {
            grid-template-columns: 1fr;
        }
    }

    &__social {
        display: flex;
        gap: 20px;
        align-items: center;
        justify-self: end;
    }

    &__title {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        span {
            font-weight: 700;
            color: rgba(252, 194, 0, 1);
        }

        @media screen and (max-width: 850px) {
            br {
                display: none;
            }
        }
    }
    
    &__link {
        display: flex;
        flex-direction: row-reverse;
        justify-content: start;
        gap: 10px;
        align-items: center;
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        transition: .3s;

        svg {
            width: 40px;
            height: 40px;
            flex-shrink: 0;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }

    &__bottom {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px;

        @include vp-1023 {
            gap: 20px;
        }

        @media screen and (max-width: 850px) {
            grid-template-columns: 1fr;
        }

    }

    .video-wrapper {
        border-radius: 15px;
        overflow: hidden;
    }
}