.intro {
    overflow: hidden;

    &__wrapper {
        padding: 110px 0 40px;
    }

    &__content {
        display: flex;
        gap: 18px;
        justify-content: space-between;

        @include vp-1023 {
            flex-direction: column;

        }
    }

    &__info {
        width: 65%;

        @include vp-1023 {
            width: 100%;
        }
    }

    &__subtext {
        font-family: 'Geologica';
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(252, 194, 0, 1);
        margin: 0 0 10px;

        @include vp-389 {
            font-size: 16px;
        }
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @include vp-1023 {
            br {
                display: none;
            }
        }

        @include vp-767 {
            font-size: 40px;
            margin-bottom: 20px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__flags {
        display: flex;
        gap: 20px;
        margin-bottom: 20px;
        img {
            width: 40px;
            height: 40px;
            object-fit: cover;
        }

        @include vp-767 {
            margin-bottom: 30px;
        }
    }

    &__list {
        display: flex;
        justify-content: space-around;
        gap: 20px;
        width: 100%;
        margin-bottom: 40px;

        @include vp-1023 {
            justify-content: space-between;
        }

        @media screen and (max-width: 630px) {
            flex-direction: column;
        }
    }

    &__item {
        position: relative;
        border-radius: 15px;
        background: transparent;
        padding: 12px 14px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        max-width: 220px;
        width: 100%;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.7);
        width: 100%;
        &::before {
          content: '';
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          border-radius: 15px;
          pointer-events: none;
          
          background: 
            linear-gradient(150deg, 
              rgba(255,255,255,0.5) 20%,
              rgba(255,255,255,0) 40%
            ),
            linear-gradient(-45deg, 
              rgba(255,255,255,0.5) 20%,
              rgba(255,255,255,0) 40%
            ),
            linear-gradient(transparent, transparent);
          -webkit-mask: 
            linear-gradient(black, black) content-box,
            linear-gradient(black, black);
          -webkit-mask-composite: xor;
          mask-composite: exclude;
          padding: 1px;
        }


        svg {
            width: 20px;
            height: 20px;
            flex-shrink: 0;
        }

        @include vp-1023 {
            max-width: 100%;
        }
    }

    &__item-text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @media screen and (max-width: 1180px) {
            br {
                display: none;
            }
        }
    }

    &__pic {
        width: 35%;

        @include vp-1023 {
            width: 100%;
        }
    }
    
    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 460/380;
        // width: 460px;
        // height: 360px;
        border-radius: 30px;
        overflow: hidden;
        position: relative;
        margin-bottom: 15px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(
                to top,
                rgba(0, 0, 0, 0.8) 0%,
                rgba(0, 0, 0, 0.4) 50%,
                rgba(0, 0, 0, 0.1) 100%
            );
            pointer-events: none;
        }

        @include vp-1023 {
            aspect-ratio: 360/200;
        }
    }

    &__image-info {
        position: absolute;
        z-index: 1;
        top: 20px;
        left: 20px;
        display: flex;
        flex-direction: column;
        height: 100%;
        padding-bottom: 40px;

        @include vp-389 {
            padding-bottom: 30px;
        }
    }

    &__name {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @include vp-389 {
            font-size: 20px;
        }
    }

    &__desc {
        margin-top: auto;
        display: flex;
        gap: 15px;
    }

    &__desc-item {
        display: flex;
        align-items: center;
        gap: 5px;

        svg {
            width: 20px;
            height: 20px;
            flex-shrink: 0;
        }

        p {
            font-family: Geologica;
            font-weight: 500;
            font-style: Medium;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color:rgba(255, 255, 255, 1);
            margin: 0;

            @include vp-389 {
                font-size: 12px;
            }
        }
    }

    &__image-bottom {
        display: flex;
        flex-direction: column;
        gap: 0px;
        align-items: flex-end;

        @include vp-1023 {
            margin-bottom: 30px;
        }
    }

    &__image-price {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        span {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color:rgba(252, 194, 0, 1);
        }

        @include vp-389 {
            font-size: 22px;
        }
    }
    
    &__image-subprice {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
    }

    &__desc-block {
        display: flex;
        justify-content: space-between;
        gap: 15px;
        margin-bottom: 20px;
        align-items: flex-start;

        @media screen and (max-width: 630px) {
            flex-direction: column;
        }
    }

    &__desc-text {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        span {
            color:rgba(252, 194, 0, 1);
        }

        @include vp-767 {
            font-size: 20px;
        }

        @include vp-389 {
            font-size: 18px;
        }
    }

    &__desc-small {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;
        position: relative;
        background: transparent;
        padding: 5px 14px;
        white-space: nowrap;
        // box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.7);
        &::before {
          content: '';
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          border-radius: 15px;
          pointer-events: none;
          
          background: 
            linear-gradient(150deg, 
              rgba(255,255,255,0.5) 20%,
              rgba(255,255,255,0) 40%
            ),
            linear-gradient(-45deg, 
              rgba(255,255,255,0.5) 20%,
              rgba(255,255,255,0) 40%
            ),
            linear-gradient(transparent, transparent);
          -webkit-mask: 
            linear-gradient(black, black) content-box,
            linear-gradient(black, black);
          -webkit-mask-composite: xor;
          mask-composite: exclude;
          padding: 1px;
        }
    }

    &__form-content {
        display: grid;
        grid-template-columns: 1fr 0.3fr;
        gap: 10px;
        
        @include vp-1023 {
            gap: 20px;
        }

        @media screen and (max-width: 930px) {
            grid-template-columns: 1fr;
        }
    }

    &__form {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 15px;

        @media screen and (max-width: 930px) {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 520px) {
            grid-template-columns: 1fr;
        }
    }

    .custom-input {
        width: 100%;
    }

    .custom-input input {
        background: rgba(255, 255, 255, 1);
        height: 50px;
        padding: 15px;
        border-radius: 10px;
        color: black;
    }

    .custom-input input::placeholder {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
    }

    &__form-btn {
        width: 100%;
        background: rgba(252, 194, 0, 1);
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(26, 26, 24, 1);
        padding: 15px;
        border-radius: 10px;
        transition: .3s;

        @media screen and (max-width: 930px) {
            grid-column: 1/-1;
        }

        @media screen and (max-width: 520px) {
            grid-column: auto;
        }
    }

    &__form-btn:hover {
        opacity: 0.8;
    }

    &__soc {
        display: flex;
        gap: 20px;
        justify-content: end;
    }

    &__what {
        display: flex;
        flex-direction: row-reverse;
        justify-content: start;
        gap: 10px;
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 0 0;
        transition: .3s;

        svg {
            width: 50px;
            height: 50px;
            flex-shrink: 0;
        }

        @media screen and (max-width: 930px) {
            margin: 0;
        }
    }

    &__what:hover {
        opacity: 0.8;
    }

    &__chek {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
        display: contents;
        a {
            display: inline;
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(120, 120, 120, 1);
            text-decoration: underline;
            transition: .3s;
        }

        a:hover {
            opacity: 0.8;
        }
    } 
}