.reviews {
    overflow: hidden;
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 5px; 


        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(252, 194, 0, 1);
        margin: 0 0 70px;

        @include vp-767 {
            margin-bottom: 40px;
        }
    }


    &__tab-list.swiper {
        width: 100%;
        overflow: hidden;
        display: flex;
    }

    .swiper-wrapper {
        height: auto;
    }

    .swiper-slide {
        display: grid;
        grid-template-columns: 0.5fr 1fr;
        gap: 40px;

        @media screen and (max-width: 850px) {
            gap: 20px;
        }

        @include vp-767 {
            display: flex;
            flex-direction: column;
        }
    }

    &__image {
    
        width: 460px;
        height: 360px;
        border-radius: 15px;
        overflow: hidden;
        display: block;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 30px;
            overflow: hidden;
        }

        @include vp-767 {
            width: 100%;
            max-width: 100%;
            height: auto;
            aspect-ratio: 460/360;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        // flex-grow: 1;

        // @include vp-767 {
        //     height: auto;
        // }
    }

    &__name {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 15px;

        @media screen and (max-width: 850px) {
            font-size: 20px;
        }
    }

    &__desc {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin-top: 0;

        @include vp-767 {
            margin-bottom: 50px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-top: auto;

        // @include vp-767 {
        //     margin-top: 0;
        // }
      
    }

    &__bname {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        color:rgba(255, 255, 255, 1);
        margin: 0;

        @media screen and (max-width: 850px) {
            font-size: 20px;
        }
    }

    &__box {
        display: flex;
        gap: 15px;

        @media screen and (max-width: 1140px) {
            flex-direction: column;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 5px;

        svg {
            width: 20px;
            height: 20px;
            flex-shrink: 0;
        }
    }

    &__city {
        font-family: Geologica;
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0;
    }
}