.work {
    &__wrapper {
        padding: 50px 0 70px;
    }

    &__top {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 40px;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(252, 194, 0, 1);
        margin: 0;
    }

    &__middle {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 40px;
        margin-bottom: 70px;

        @include vp-1023 {
            gap: 20px;
        }

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: #181817;
        background-blend-mode: overlay;
        display: flex;
        flex-direction: column;
        position: relative;
        border-radius: 15px;

        padding: 20px;
        width: 100%;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.7);
        width: 100%;
        &::before {
            content: '';
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            border-radius: 15px;
            pointer-events: none;
            
            background: 
              linear-gradient(150deg, 
                rgba(255,255,255,0.5) 0%,
                rgba(255,255,255,0) 20%
              ),
              linear-gradient(-45deg, 
                rgba(255,255,255,0.5) 20%,
                rgba(255,255,255,0) 25%
              ),
              linear-gradient(transparent, transparent);
            -webkit-mask: 
              linear-gradient(black, black) content-box,
              linear-gradient(black, black);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
            padding: 1px;
        }

    }

    &__icon {
        width: 40px;
        height: 40px;
        margin-bottom: 10px;
        img {
            width: 100px;
            height: 100%;
            object-fit: cover;
        }
    }

    &__city {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 22px;
        }
    }

    &__adress {
        display: flex;
        gap: 90px;

        .work__adress-item p,
        .work__adress-item a {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color:rgba(255, 255, 255, 1);
            margin: 0;

            @include vp-389 {
                font-size: 12px;
            }
        }

        @include vp-1023 {
            gap: 40px;
        }

        @include vp-389 {
            gap: 20px;
        }

        .work__adress-item a {
            margin-top: 10px;
            transition: .3s;
        }

        .work__adress-item a:hover {
            opacity: 0.8;
        }
    }

    &__bottom-text {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 20px;
        
        span {
            font-weight: 700;
            color: rgba(252, 194, 0, 1);
        }

        @include vp-767 {
            font-size: 20px;
        }
    }

    &__form {
        display: flex;
        gap: 20px;
        margin-bottom: 15px;

        @include vp-767 {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 520px) {
            grid-template-columns: 1fr;
        }
    }

    .custom-input {
        width: 100%;
    }

    .custom-input input {
        background: rgba(255, 255, 255, 1);
        height: 50px;
        padding: 15px;
        border-radius: 10px;
        color: black;
    }

    .custom-input input::placeholder {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
    }

    &__form-btn {
        max-width: 275px;
        width: 100%;
        background: rgba(252, 194, 0, 1);
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        padding: 14px;
        border-radius: 10px;
        color: rgba(26, 26, 24, 1);
        transition: .3s;

        @include vp-767 {
           max-width: 100%;
           grid-column: 1/-1;
        }

        @media screen and (max-width: 520px) {
            grid-column: auto;
        }
    }

    &__form-btn:hover {
        opacity: 0.8;
    }

    &__chek {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
        display: contents;
        a {
            display: inline;
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(120, 120, 120, 1);
            text-decoration: underline;
            transition: .3s;
        }

        a:hover {
            opacity: 0.8;
        }
    } 
}