.risk {
    &__wrapper {
        padding: 40px 0;
        display: flex;
        align-items: center;
        gap: 30px;

        @media screen and (max-width: 850px) {
            flex-direction: column;
        }
    }


    &__info {
        width: 100%;
        span {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(252, 194, 0, 1);
            display: block;
            margin-bottom: 5px;

            @include vp-389 {
                font-size: 16px;
            }
        }
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;
        
        @media screen and (max-width: 1274px) {
            br {
                display: none;
            }
        }

        @include vp-1023 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        @media screen and (max-width: 850px) {
            br {
                display: none;
            }
        }
    }

    &__link {
        background-color: rgba(252, 194, 0, 1);
        max-width: 275px;
        width: 100%;
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(26, 26, 24, 1);
        padding: 14px;
        border-radius: 10px;
        transition: .3s;

        @media screen and (max-width: 850px) {
            max-width: 100%;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 460/520;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media screen and (max-width: 850px) {
            aspect-ratio: 175/200;
        }
    }
}