.how {
    overflow: hidden;
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;
        
        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        @include vp-389 {
            font-size: 16px;
        }
    }

    &__content {
        @media screen and (max-width: 860px) {
            display: none;
        }
    }

    &__top {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px;
        align-items: start;

        @media screen and (max-width: 1059px) {
            gap: 20px;
        }
    }

    &__item {
        max-width: 360px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        border-bottom: 1px solid rgba(120, 120, 120, 1);
        padding-bottom: 15px;
    }

    &__num {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(252, 194, 0, 1);

        @include vp-767 {
            font-size: 40px;
        }
    }

    &__name {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0;

        @include vp-767 {
            font-size: 22px;
        }
    }

    &__desc {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
    }

    &__icons {
        display: flex;
        align-items: center;
        max-width: 1180px;
        width: 100%;
        justify-content: space-between;
        position: relative;
        margin-top: 40px;
        margin-bottom: 40px;
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: 12.5px;
          right: 12.5px; 
          height: 1px;
          background: rgba(120, 120, 120, 1);
          transform: translateY(-50%);
          z-index: 0;
        }
    
        
        .icon-wrapper {
          position: relative;
          display: flex;
          justify-content: center;
          width: 20px;
          height: 20px;
        }
    
        .icon-wrapper:nth-child(1)::before,
        .icon-wrapper:nth-child(3)::before,
        .icon-wrapper:nth-child(5)::before {
          content: '';
          position: absolute;
          top: -40px;
          width: 1px;
          height: 40px;
          background: rgba(120, 120, 120, 1);
          z-index: -1;
        }

        .icon-wrapper:nth-child(2)::before,
        .icon-wrapper:nth-child(4)::before {
            content: '';
            position: absolute;
            top: 20px;
            width: 1px;
            height: 40px;
            background: rgba(120, 120, 120, 1);
            z-index: -1;
        }
    
        .icon-wrapper:nth-child(1)::before {
          top: -60px;
          height: 60px;

          @media screen and (max-width: 1099px) {
            top: -40px;
          }

          @media screen and (max-width: 940px) {
            top: -48px;
          }
        }

        .icon-wrapper:nth-child(3)::before {
            @media screen and (max-width: 940px) {
                top: -48px;
                height: 50px;
            }
            
        }
    
        .icon-wrapper:nth-child(5)::before {
          top: -60px;
          height: 60px;

            @media screen and (max-width: 940px) {
                top: -40px;
                height: 50px;
            }
        }
    
        svg {
          width: 20px;
          height: 20px;
          flex-shrink: 0;
          position: relative;
          z-index: 1;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-around;
        gap: 20px;

        .how__item {
            border-bottom: none;
            border-top: 1px solid rgba(120, 120, 120, 1);
            padding-top: 15px;
        }
    }

    &__content-mobile {
        display: none;

        @media screen and (max-width: 860px) {
            display: flex;
            flex-direction: column;
            gap: 30px;

            .how__item {
                max-width: 100%;
            }
        }
    }
}