.complect {
    position: relative;
    overflow: hidden;
    box-shadow:
    inset 0 -8px 5px -8px rgba(179,179,179,1);
    // 0 -10px 20px -10px rgba(242,242,242,0.5),
    border: none; 
    border-radius: 0 0 30px 30px;
    margin-bottom: 30px;
    background: rgba(33, 33, 32, 1);
    .boat-shape {
        fill: transparent;
        stroke: rgba(242,242,242,0.5);
        position: absolute;
    }

    &__wrapper {
        padding: 210px 0 70px;
        display: flex;  
        justify-content: space-between;
        gap: 20px;

        @include vp-767 {
            padding: 120px 0 50px;
            flex-direction: column;
            gap: 40px;
        }
    }

    &__info {
        max-width: 560px;
        width: 100%;

        span {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(252, 194, 0, 1);
            display: block;
            margin-bottom: 27px;
        }

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;

        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
    }

    &__form {
        max-width: 460px;
        width: 100%;

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__form-content {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 20px;
    }

    .custom-input {
        width: 100%;
        
    }

    .custom-input input {
        background: rgba(255, 255, 255, 1);
        color: black;
        padding: 15px;
        border-radius: 10px;
    }

    .custom-input input::placeholder {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);

    }

    &__btn {
        max-width: 275px;
        width: 100%;
        background: rgba(252, 194, 0, 1);
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(26, 26, 24, 1);
        padding: 15px;
        border-radius: 10px;
        transition: .3s;
        margin-left: auto;
        display: block;

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__chek {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin-left: auto;
        width: 100%;
        max-width: 275px;
       
        a {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            color: rgba(120, 120, 120, 1);
            text-decoration: underline;
            transition: .3s;
            display: inline;
        }

        @include vp-767 {
            max-width: 100%;
            text-align: left;

            br {
                display: none;
            }
        }

        a:hover {
            opacity: 0.8;
        }
    }
}