.profit {
    position: relative;
    overflow: hidden;
    box-shadow:
    inset 0 -8px 5px -8px rgba(179,179,179,1);
    // 0 -10px 20px -10px rgba(242,242,242,0.5),
    border: none; 
    border-radius: 0 0 30px 30px;
    margin-bottom: 30px;
    background: rgba(33, 33, 32, 1);
    .boat-shape {
        fill: transparent;
        stroke: rgba(242,242,242,0.5);
        position: absolute;
    }
    
  
    &__wrapper {
        padding: 200px 0 70px;
        position: relative;

        @include vp-767 {
            padding: 100px 0 50px;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 60px;
        gap: 40px;
    }
    
    &__text-block {
        display: flex;
        flex-direction: column;

        span {
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color:rgba(255, 255, 255, 1);
        }
    }

    &__subtext {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(252, 194, 0, 1);
        margin: 0 0 5px;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;

        @include vp-767 {
            font-size: 40px;
        }
    }

    &__item {
        max-width: 360px;
        svg {
            width: 50px;
            height: 50px;
            flex-shrink: 0;
            margin-bottom: 15px;
        }

        &--mobile {
            display: none;

            @include vp-767 {
                display: block;
            }
        }

        &--desc {
            @include vp-767 {
                display: none;
            }
        }

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__name {
        font-family: Geologica;
        font-weight: 500;
        font-style: Medium;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @include vp-767 {
            font-size: 20px;
        }
    }

    &__desc {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px;

        @include vp-1023 {
            grid-template-columns: 1fr 1fr;
        
            .profit__item:nth-child(odd) {
              justify-self: end;
            }
        }

        @include vp-767 {
            display: flex;
            flex-direction: column;
        }
    }
}