.comand {
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        span {
            color: rgba(252, 194, 0, 1);
        }

        @include vp-767 {
            font-size: 40px;
        }
    }

    &__text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        max-width: 460px;
        width: 100%;
        margin: 0 0 40px;

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px;
        
        @include vp-1023 {
            gap: 20px;
        }
        
        @include vp-767 {
            grid-template-columns: 1fr;
            
        }
    }

    &__item {
        position: relative;
        border-radius: 30px;
        overflow: hidden;

        @include vp-767 {
            border-radius: 50px;
        }
    }

    &__item::after {
        position: absolute;
        content: '';
        background: linear-gradient(180deg, rgba(26, 26, 24, 0) 67.36%, #1A1A18 95.25%);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 0;
        box-shadow: -2px -2px 4px -2px rgba(179, 179, 179, 1) inset,
        2px 2px 4px -2px rgba(179, 179, 179, 1) inset;
        // border-radius: 30px;
        // overflow: hidden;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 360/400;
        border-radius: 30px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__info {
        position: absolute;
        bottom: 25px;
        left: 20px;
        z-index: 1;
        display: flex;
        flex-direction: column;
        gap: 5px;
        padding-right: 20px;
    }

    &__name {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @include vp-1023 {
            font-size: 22px;
        }
    }

    &__work {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(252, 194, 0, 1);
        margin: 0;
    }
}