.level {
    overflow: hidden;
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        span {
            font-weight: 700;
            color: rgba(252, 194, 0, 1);
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px;
        margin-bottom: 70px;

        @include vp-1023 {
            gap: 20px;
        }

        @media screen and (max-width: 900px) {
            grid-template-columns: 1fr 1fr;
            gap: 40px 20px;
        }

        @media screen and (max-width: 520px) {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        position: relative;
        border-radius: 15px;
        background: rgba(33, 33, 32, 1);
        background-blend-mode: overlay;

        padding: 20px;
        width: 100%;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.7);
        width: 100%;
        
        &::before {
          content: '';
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          border-radius: 15px;
          pointer-events: none;
          
          background: 
            linear-gradient(150deg, 
              rgba(255,255,255,0.5) 0%,
              rgba(255,255,255,0) 20%
            ),
            linear-gradient(-45deg, 
              rgba(255,255,255,0.5) 20%,
              rgba(255,255,255,0) 25%
            ),
            linear-gradient(transparent, transparent);
          -webkit-mask: 
            linear-gradient(black, black) content-box,
            linear-gradient(black, black);
          -webkit-mask-composite: xor;
          mask-composite: exclude;
          padding: 1px;
        }

        &--big {
            background: #181817;
            background-blend-mode: overlay;
            position: relative;

            @media screen and (max-width: 900px) {
                grid-column: 1/-1;
            }
        }
    }

    &__present {
        position: absolute;
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(26, 26, 24, 1);
        padding: 12px;
        background: rgba(252, 194, 0, 1);
        border-radius: 10px;
        max-width: 215px;
        margin: 0;
        display: block;
        transform: rotate(10deg);
        right: -30px;
        top: -40px;

        @media screen and (max-width: 900px) {
            top: -10px;
            right: 0;
            transform: rotate(5deg);
        }

        @media screen and (max-width: 450px) {
            font-size: 12px;
            max-width: 140px;
            padding: 10px;
        }

        @include vp-389 {
            position: static;
            transform: rotate(0);
            margin-bottom: 20px;
            max-width: 200px;
        }
    }

    &__name {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 5px;
    }

    &__desc {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: 0 0 15px;
    }

    &__sublist {
        list-style: disc;
        display: flex;
        flex-direction: column;
        gap: 10px;
        padding-left: 20px;
        margin-bottom: 30px;
        li {
            list-style: disc;
            font-family: Geologica;
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 150%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(255, 255, 255, 1);
        }

        li::marker {
            color:rgba(252, 194, 0, 1);
        }
    }

    &__dop {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(120, 120, 120, 1);
        margin: auto 0 15px;
        border-top: 1px solid rgba(252, 194, 0, 1);
        padding-top: 15px;
    }

    &__btn {
        max-width: 275px;
        width: 100%;
        background: rgba(252, 194, 0, 1);
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        padding: 15px;
        border-radius: 10px;
        color: rgba(26, 26, 24, 1);
        transition: .3s;
        margin: 0 auto;
        display: block;
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__bottom {
        display: grid;
        grid-template-columns: 2.2fr 1fr;
        gap: 20px;
        align-items: start;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__bottom-text {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        span {
            font-weight: 700;
            color: rgba(252, 194, 0, 1);
        }

        @include vp-767 {
            font-size: 20px;
        }

        @include vp-389 {
            font-size: 16px;
        }
    }

    &__bottom-link {
        display: flex;
        flex-direction: row-reverse;
        justify-content: start;
        align-items: center;
        gap: 10px;
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        transition: .3s;

        svg {
            width: 50px;
            height: 50px;
            flex-shrink: 0;
        }
    }

    &__bottom-link:hover {
        opacity: 0.8;
    }
}