.answer {
    position: relative;
    overflow: hidden;
    box-shadow:
    inset 0 -8px 5px -8px rgba(179,179,179,1);
    // 0 -10px 20px -10px rgba(242,242,242,0.5),
    border: none; 
    border-radius: 0 0 30px 30px;
    margin-bottom: 30px;
    background: rgba(33, 33, 32, 1);

    .boat-shape {
        fill: transparent;
        stroke: rgba(242,242,242,0.5);
        position: absolute;
    }


    &__wrapper {
        padding: 150px 0 70px;
        position: relative;
        display: grid;
        grid-template-columns: 1fr 0.7fr;
        gap: 20px;

        @media screen and (max-width: 920px) {
            grid-template-columns: 1fr;
        }

        @include vp-767 {
            padding: 100px 0 50px;
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
    }

    &__title {
        font-family: ActayWide;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        @include vp-767 {
            font-size: 40px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    .accordion {
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 560px;
        text-align: left !important;
        transition: .3s;

        @media screen and (max-width: 920px) {
            max-width: 100%;
        }
    }

    .accordion__button {
        cursor: pointer;
        // transition: .3s;
    }

    .accordion__button:hover {
        opacity: 0.8;
    }

    .accordion__button span {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        display: block;
        text-align: left;
        margin-bottom: 20px;

        @include vp-767 {
            font-size: 22px;
        }
    }

    .accordion__inner {
        font-family: Geologica;
        font-weight: 300;
        font-style: Light;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        color:rgba(255, 255, 255, 1);
        display: block;
        text-align: left;
    }

    .accordion__inner ul {
        list-style: disc;
        padding-left: 20px;
    }

    .accordion__inner ul li {
        list-style: disc;
    }

    .accordion__item.is-active .accordion__button span {
        margin-bottom: 20px;
    }

    &__right-text {
        font-family: Geologica;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 150%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 100px 0 0;

        @media screen and (max-width: 920px) {
            margin-top: 70px;
        }
    }

}