.footer {
  background: rgba(252, 194, 0, 1);
  overflow: hidden;
  
  &__wrapper {
    padding: 50px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;

    @media screen and (max-width: 980px) {
      flex-wrap: wrap;
      gap: 50px 20px;
    }

    @include vp-767 {
      flex-direction: column;
      
    }

    @media screen and (max-width: 520px) {
      justify-content: center;
    }
  }

  &__left {
    display: flex;
    gap: 15px;
    align-items: center;

   

    @include vp-767 {
      flex-direction: column;
    }
  }

  &__icons {
    display: flex;
    gap: 15px;

    a {
      svg {
        transition: .3s;
        width: 33px;
        height: 33px;
        flex-shrink: 0;
      }
    }
  }

  &__icons a:hover {
    svg {
      opacity: 0.8;
    }
  }

  &__data {
    display: flex;
    // flex-direction: column;
    gap: 20px;

    a {
      display: block;
      font-family: Geologica;
      font-weight: 400;
      font-style: Regular;
      font-size: 14px;
      leading-trim: CAP_HEIGHT;
      line-height: 100%;
      letter-spacing: 0%;
      vertical-align: middle;
      color: rgba(26, 26, 24, 1);
      transition: .3s;
    }

    a:hover {
      opacity: 0.8;
    }

    @include vp-767 {
      flex-direction: column;
    }
  }



  &__right a {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: start;
    transition: .3s;
  }

  &__right a:hover {
    opacity: 0.8;
  }

  &__center {
    a {
      font-family: Geologica;
      font-weight: 400;
      font-style: Regular;
      font-size: 14px;
      leading-trim: CAP_HEIGHT;
      line-height: 150%;
      letter-spacing: 0%;
      vertical-align: middle;
      color: rgba(26, 26, 24, 1);
      transition: .3s;
    }

    a:hover {
      opacity: 0.8;
    }

    @media screen and (max-width: 672px) {
      margin: 0 auto;
    }
  }

  &__doc {
    font-family: Geologica;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 150%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: rgba(26, 26, 24, 1);
    margin: 0;
  }


}